<template>
	<v-card-actions>
		<v-btn text color="grey"  @click="vote('solidarity', commentId)">
			<v-icon left>$vuetify.icons.solidarity</v-icon><span class="d-none d-sm-flex">Show Solidarity </span>({{counts.solidarity ? counts.solidarity : '0'}})
		</v-btn>
		<v-btn text color="grey" @click="vote('empathize', commentId)">
			<v-icon left>$vuetify.icons.empathy</v-icon><span class="d-none d-sm-flex">Empathize </span>({{counts.empathize ? counts.empathize : '0'}})
		</v-btn>
		<v-spacer></v-spacer>
		<template>
			<div class="text-center d-flex align-center mr-5">
				<v-tooltip bottom>
					<template v-slot:activator="{ on, attrs }">
						<v-btn icon @click="vote('flag', commentId)" :disabled="flagged" v-bind="attrs" v-on="on" :color="counts.flag ? 'error' : 'grey'">
							<v-icon>mdi-flag</v-icon> <span v-if="counts.flag">({{counts.flag}})</span>
						</v-btn>
					</template>
					<span>Flag this post as inappropriate</span>
				</v-tooltip>
			</div>
		</template>

		<v-btn v-if="this.$route.query.adminKey"  color="error" @click="deleteComment(commentId)" :disabled="removed">
			<v-icon left>mdi-cancel</v-icon>{{removed ? 'Comment Removed' : 'Admin Remove'}}
		</v-btn>
		<v-snackbar v-model="snackbar.show">{{snackbar.text}}</v-snackbar>
	</v-card-actions>
</template>

<script>
	import Vue from 'vue';
	import { VueReCaptcha } from 'vue-recaptcha-v3';
	Vue.use(VueReCaptcha, { siteKey: '6LeSLqUZAAAAAFH3qdzPtcrMyMNnL-bgcxQuuizs' });
	export default {
		name: "Votes",
		props: ['commentId', 'empathizeCount', 'solidarityCount'],
		data: () => ({
			counts: {
				solidarity: 0,
				empathize: 0,
				flag: 0
			},
			snackbar: {
				show: false,
				text: null
			},
			flagged: false,
			removed: false
		}),
		methods: {
			async deleteComment(id) {
				try {
					await this.$http.delete(`${this.$api}/comments/${id}?adminKey=${this.$route.query.adminKey}`)
					this.removed = true
				} catch (e) {
					console.error(e)
					this.snackbar.text = e
					this.snackbar.show = true
				}
			},
			async vote(type, commentId) {
				const captcha = await this.reCaptcha()
				await this.$http.post(this.$api + '/votes', {
					voteType: type,
					commentId: commentId,
					captcha: captcha
				})
				await this.getVotes()
				if (type === 'flag') {
					this.snackbar.text = 'Comment flagged for review'
					this.snackbar.show = true
					this.flagged = true
				}
			},
			async getVotes() {
				try {
					let results
					if (this.$route.query.adminKey) {
						results = await this.$http.get(this.$api + '/votes/count/admin?commentId=' + this.commentId + '&adminKey=' + this.$route.query.adminKey)
					} else {
						results = await this.$http.get(this.$api + '/votes/count?commentId=' + this.commentId)
					}
					this.counts = results.data
				} catch (e) {
					console.error(e)
					this.snackbar.text = e
					this.snackbar.show = true
				}
			},
			async reCaptcha(){
				try{
					await this.$recaptchaLoaded();
					return await this.$recaptcha('login')
				} catch (e) {
					console.log(`#reCaptcha: ${e}`);
				}
			},
		},
		created() {
			this.counts.solidarity = this.solidarityCount
			this.counts.empathize = this.empathizeCount
		}
	}
</script>

